<template>
	<div class="loginHeader">
		{{title}}
		<span>
			<em>Member login</em>
			<br/>
			
		</span>
		<ul>
			<li v-for="tip in tips" :key="tip.id">
				{{tip}}
				<i class="iconfont icon-right"></i>
			</li>
		</ul>
	</div>
</template>

<script>
export default{
	props:{
		title:String,
		tips:Array
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	.loginHeader
		padding 10px 60px 0 40px
		width 100%
		height 65px
		background url("../../../static/img/c1.jpg") no-repeat
		font-size 30px
		span
			padding 5px 0px 14px 40px
			background url("../../../static/img/c-t.png") no-repeat left center
			color #b19365
			font-size 13px
			display inline-block
		ul
			display inline-block
			font-size 15px
			float right
			padding 15px 0
			margin 0
			li
				list-style none
				float left
			li:last-child
				i
					display none
</style>


