<!--  -->
<template>
	<div>
		<div class="headLine">
			<div>
				<span class="headLeft">
					
					[<router-link to="/login">&nbsp;&nbsp;</router-link>]
					[<router-link to="/register"><span class="iconfont icon-bianjishuru"></span>&nbsp;&nbsp;</router-link>]
				</span>
				
				<span class="headRight">
					<em class="iconfont icon-dianhua-yuankuang"></em>
					<span>
						<i>Service hotline</i>
						400-021-1234
					</span>
				</span>
			</div>
		</div>
		
		<div class="headTop">
			<div>
				<img src="./img/logo.png">
				<div class="top">
					<div :class="{image:current==1,img:$route.meta.show==1}">
						<router-link to="/homepage" @mouseover.native="addClass(1,1)" @mouseout.native="removeClass"></router-link>
					</div>
					<div :class="{image:current==2,img:$route.meta.show==2}">
						<router-link to="/knowwe" @mouseover.native="addClass(2,2)"></router-link>
						<ul :class="{show:isShow==2}" @mouseover="addClass(2,2)" @mouseout="removeClass">
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
						</ul>
					</div>
					<div :class="{image:current==3,img:$route.meta.show==3}">
						<router-link to="/showroom" @mouseover.native="addClass(3,3)"></router-link>
						<ul :class="{show:isShow==3}" @mouseover="addClass(3,3)" @mouseout="removeClass">
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
						</ul>
					</div>
					<div :class="{image:current==4,img:$route.meta.show==4}">
						<router-link to="/hamburg" @mouseover.native="addClass(4,4)"></router-link>
						<ul :class="{show:isShow==4}" @mouseover="addClass(4,4)" @mouseout="removeClass">
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
						</ul>
					</div>
					<div :class="{image:current==5,img:$route.meta.show==5}">
						<router-link to="/news" @mouseover.native="addClass(5,5)"></router-link>
						<ul :class="{show:isShow==5}" @mouseover="addClass(5,5)" @mouseout="removeClass">
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
						</ul>
					</div>
					<div :class="{image:current==6,img:$route.meta.show==6}">
						<router-link to="/milktea" @mouseover.native="addClass(6,6)"></router-link>
						<ul :class="{show:isShow==6}" @mouseover="addClass(6,6)" @mouseout="removeClass">
							<li><a href="#"></a></li>
						</ul>
					</div>
					<div :class="{image:current==7,img:$route.meta.show==7}">
						<router-link to="/services" @mouseover.native="addClass(7,7)"></router-link>
						<ul :class="{show:isShow==7}" @mouseover="addClass(7,7)" @mouseout="removeClass">
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
						</ul>
					</div>
					<div :class="{image:current==8,img:$route.meta.show==8}">
						<router-link to="/contactus" @mouseover.native="addClass(8,8)"></router-link>
						<ul :class="{show:isShow==8}" @mouseover="addClass(8,8)" @mouseout="removeClass">
							<li><a href="#"></a></li>
							<li><a href="#"></a></li>
						</ul>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
export default{
	data:function(){
		return {
			current:0,
			isShow:0
		}
	},
	methods:{
		addClass:function(img,isshow){
			this.current=img;
			this.isShow=isshow;
		},
		removeClass:function(){
			this.current=0;
			this.isShow=0;
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.headLine
	background black
	color white
	padding 15px 0
	font-size 13px
	div
		height 24px
		line-height 24px
		width:60%
		margin auto
		a
			color yellow
		a:hover
			cursor pointer
		.headRight
			float right
			em
				font-size 40px
			span
				display inline-block
				font-size 20px
				i
					display block
					line-height 10px
					font-size 13px
.headTop
	background url(./img/header.jpg) no-repeat center
	height 85px
	div
		width 70%
		margin 0 auto
		.top
			display inline-block
			margin-left 5%
			.image
				background url(./img/navbg.png) no-repeat center
			.img
				background url(./img/navbg.png) no-repeat center
			div
				display inline-block
				width 10%
				margin-right 1%
				text-align center
				font-size 20px
				height 40px
				line-height 40px
				border-radius 8px
				a
					color black
					text-decoration:none
				.show
					display block
				ul
					width 450px
					display none
					li
						float left
						padding-left 15px
						list-style none
						font-size 10px
						a:hover
							color #dcbe28
							text-decoration underline
</style>


