<!--  -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="./img/banner-Affiliate.png">
			</div>
		</div>
		
		<div class="hamburgCantainer">
			<div class="hamburgCantainer2">
				<LoginHeader title="" :tips="tips"></LoginHeader>
				
				<div class="hamburgBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="hamburgRight">
						<div></div>
						
						<div>
							<div>
								<h1></h1>
								<p></p>
								<p><span>60%</span></p>
							</div>
							
							<p>1  </p>
							<p>2 </p>
							<p>3</p>
							<p>4</p>
							<p>510 </p>
							<p>6</p>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["","",""],
			entry:[
				{
					message:"",
					hover:1,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.hamburgCantainer
	width 100%
	height 950px
	background url("../../../static/img/container.jpg")
	.hamburgCantainer2
		width 58%
		height 950px
		margin 0 auto
		.hamburgBody
			padding 20px 55px 20px 40px
			width 100%
			height 790px
			background url("../../../static/img/c2.jpg") repeat-y
			.hamburgRight
				width 80%
				float right
				div:nth-child(1)
					font-size 20px
					border-bottom 1px dashed #c19671
					margin-bottom 25px
				div:nth-child(2)
					div
						background url("./img/img4.png") no-repeat right top
						height 240px
						padding-top 20px
						h1
							margin-bottom 35px
						p
							font-size 15px
							margin 0
							span
								font-size 35px
								color #900
								vertical-align middle
								font-style italic
					p
						font-size 15px
						margin 5px 0
						line-height 30px
</style>


