<!--  -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="../ContactUs/img/banner-Contact.png">
			</div>
		</div>
		
		<div class="cantactCantainer">
			<div class="cantactCantainer2">
				<LoginHeader title="" :tips="tips"></LoginHeader>
				
				<div class="ContactBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="LeaveRight">
						<div>
							
						</div>
						
						<div>
							<h1></h1>
							
							<br>
							
							<div></div>
						</div>
						
						<div>
							<table>
								<tbody>
									<tr>
										<th></th>
										<td>
											<input type="text" />
											
											<select>
												<option></option>
												<option></option>
												<option></option>
											</select>
										</td>
										<th></th>
										<td><input type="text" /></td>
									</tr>
									<tr>
										<th></th>
										<td><input type="checkbox" /></td>
										<th>QQ</th>
										<td><input type="text" /></td>
									</tr>
									<tr>
										<th></th>
										<td colspan="3">
											<select>
												<option></option>
											</select>
											<select>
												<option></option>
											</select>
										</td>
									</tr>
									<tr>
										<th></th>
										<td colspan="3">
											<select>
												<option></option>
											</select>
											<label>
												<textarea></textarea>
											</label>
											<p>
												<button type="button" class="btn btn-info"></button>
											</p>
										</td>
									</tr>
								</tbody>
							</table>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["","",""],
			entry:[
				{
					message:"",
					hover:0,
					skip:"/contactus"
				},
				{
					message:"",
					hover:1,
					skip:"/leavewords"
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.LeaveRight
	float right
	width 80%
	div:nth-child(1)
		font-size: 20px;
		border-bottom: 1px dashed #c19671;
		margin-bottom: 25px;
	div:nth-child(2)
		color #900
		height 200px
		font-size 15px
		position relative
		h1
			font-size 20px
		div
			display inline-block
			width 176px
			height 152px
			position absolute
			right 60px
			top 0
			background url("./img/edit.png")
	div:nth-child(3)
		tr
			height 35px
			font-size 15px
			th
				text-align right
				padding 5px
			td
				padding 5px
				input,select,textarea
					border 1px solid #dfc1a7
				label
					display block
					margin-top 10px
					width 355px
					height 105px
					textarea
						width 355px
						height 105px
						resize none//
		tr:nth-child(4)
			select
				width 355px
</style>


