<!--  -->
<template>
	<div>
		<div class="warp">
			<div class="swiper-container" id="swiper">
				<div class="swiper-wrapper">
					<div class="swiper-slide"><img src="./img/img19.png" /></div>
					<div class="swiper-slide"><img src="./img/img19.png" /></div>
					<div class="swiper-slide"><img src="./img/img19.png" /></div>
				</div>
				<div class="swiper-pagination"></div>
			</div>
		</div>
		
		
		
		<div>
			<div class="swiper-container" id="footSwiper">
				<div class="swiper-wrapper">
					<div class="swiper-slide"><img src="./img/img16.jpg" /></div>
					<div class="swiper-slide"><img src="./img/img17.jpg" /></div>
					<div class="swiper-slide"><img src="./img/img18.jpg" /></div>
					<div class="swiper-slide"><img src="./img/img18.jpg" /></div>
					<div class="swiper-slide"><img src="./img/img17.jpg" /></div>
					<div class="swiper-slide"><img src="./img/img16.jpg" /></div>
				</div>
				<div class="swiper-button-prev"></div>
				<div class="swiper-button-next"></div>
			</div>
		</div>
	</div>
</template>

<script>
import Swiper from 'swiper'
export default{
	mounted(){
		var mySwiper = new Swiper("#swiper",{
			autoplay:true,
			loop:true,
			pagination:{
				el:".swiper-pagination"
			}
		})
		
		var footSwiper = new Swiper("#footSwiper",{
			autoplay:true,
			loop:true,
			slidesPerView:3,
			slidePerGroup:3,
			pagination:{
				el:"footSwiper"
			},
			navigation:{
				prevEL:".swiper-button-prev",
				nextEl:".swiper-button-next"
			}
		})
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.warp
	background url("./img/bannerbg.jpg")
	#swiper
		width 58%
		margin 0 auto
	.swiper-pagination-bullet
		width 30px
		border-radius 5px
	.swiper-pagination-bullet-active
		background red
#footSwiper
	width 62%
	margin 0 atuo
	.swiper-wrapper
		width 100%
		height 376px
		padding-left 35px
		.swiper-slide
			width 350px
			display flex
</style>


