<!--  -->
<template>
	<div>
		<BannerMsg></BannerMsg>
		<div class="loginCantainer">
			<div class="loginCantainer2">
				<LoginHeader title=""></LoginHeader>
				
				<div class="loginBody">
					<div class="loginInfo">
						<table>
							<tbody>
								<tr>
									<td class="td1"></td>
									<td class="td2"><input type="text" /></td>
								</tr>
								<tr>
									<td class="td1"></td>
									<td class="td2"><input type="text" /></td>
								</tr>
								<tr>
									<td class="td1"></td>
									<td class="check">
										<input type="text" />
										<img src="../../../static/img/img14.jpg" />
										<a href="#"></a>
									</td>
								</tr>
								<tr>
									<td class="td1">&nbsp;</td>
									<td>
										<input type="checkbox">
										
									</td>
								</tr>
								<tr>
									<td class="td1">&nbsp;</td>
									<td>
										<input type="button" class="btn btn-info" value="">
									</td>
								</tr>
							</tbody>
						</table>
					</div>
					
					<div class="toRegister">
						<p><span></span></p>
						<p>
							
							<br>
							3D
						</p>
						<p>
							<router-link class="btn btn-success" to="/register"></router-link>
						</p>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import BannerMsg from "../../components/BannerMsg/BannerMsg.vue"
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
export default{
	components:{
		BannerMsg,
		LoginHeader,
		LoginFoot
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	.loginCantainer
		width 100%
		height 550px
		background url("../../../static/img/container.jpg")
		.loginCantainer2
			width 58%
			height 550px
			margin 0 auto
			.loginBody
				width 100%
				height 350px
				background url("../../../static/img/c2.jpg") repeat-y
				.loginInfo
					margin 50px 0px 0px 20px
					width 50%
					height 210px
					display inline-block
					border-right 1px dashed #c19671
					table
						tr
							height 50px
							padding 5px
							.td1
								width 30%
								text-align right
								padding 0 15px
								font-size 17px
							.td2
								width 70%
								input
									width 100%
									height 38px
							.check
								width 70%
								input
									vertical-align:middle
									width 50%
									height 38px
								img
									vertical-align:middle
								a
									font-size 8px
									color black
							.btn
								width 50%
				.toRegister
					margin 50px 15px 0px 0px
					width 39%
					height 210px
					float right
					display inline-block
					font-size 14px
					span
						color red
</style>


