<!--  -->
<template>
	<div>
		<BannerMsg></BannerMsg>
		<div class="registerCantainer">
			<div class="registerCantainer2">
				<LoginHeader title=""></LoginHeader>
				
				<div class="registerBody">
					<div class="registerInfo">
						<table>
							<tbody>
								<tr>
									<td></td>
									<td><input type="text" v-model="username" @blur="checkUsername" /></td>
									<td>
										<img :src="usernameSrc">
										{{usernameTips}}
									</td>
								</tr>
								<tr>
									<td></td>
									<td><input type="password" v-model="pw" @blur="checkPw" /></td>
									<td>
										<img :src="pwSrc">
										{{pwTips}}
									</td>
								</tr>
								<tr>
									<td></td>
									<td><input type="password" v-model="pw2" @blur="checkPw2" /></td>
									<td>
										<img :src="pw2Src">
										{{pw2Tips}}
									</td>
								</tr>
								<tr>
									<td></td>
									<td><input type="text" v-model="phone" @blur="checkPhone" /></td>
									<td>
										<img :src="phoneSrc">
										{{phoneTips}}
									</td>
								</tr>
								<tr>
									<td>QQ</td>
									<td><input type="text" v-model="QQ" @blur="checkQQ" /></td>
									<td>
										<img :src="QQSrc">
										{{QQTips}}
									</td>
								</tr>
								<tr>
									<td></td>
									<td>
										<input name="code" type="text" v-model="code" @blur="checkCode" />
										<img src="../../../static/img/img14.jpg" />
										<a href="#"></a>
									</td>
									<td>
										<img :src="codeSrc">
										{{codeTips}}
									</td>
								</tr>
								<tr>
									<td>&nbsp;</td>
									<td>
										<button type="button" class="btn btn-info" @click="register"></button>
									</td>
									<td>&nbsp;</td>
								</tr>
							</tbody>
						</table>
					</div>
					
					<div class="toLogin">
						<p><span></span></p>
						<p>
							
							<br>
							3D
						</p>
						<p>
							<router-link class="btn btn-success" to="/login"></router-link>
						</p>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import BannerMsg from "../../components/BannerMsg/BannerMsg.vue"
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
export default{
	data:function(){
		return{
			username:"",
			pw:"",
			pw2:"",
			phone:"",
			QQ:"",
			code:"",
			usernameTips:"",
			pwTips:"",
			pw2Tips:"",
			phoneTips:"",
			QQTips:"",
			codeTips:"",
			usernameSrc:"",
			pwSrc:"",
			pw2Src:"",
			phoneSrc:"",
			QQSrc:"",
			codeSrc:""
		}
	},
	components:{
		BannerMsg,
		LoginHeader,
		LoginFoot
	},
	methods:{
		checkUsername:function(){
			if(this.username==null||this.username==""||this.username==undefined){
				this.usernameTips="";
				this.usernameSrc="../../../static/img/ico1.png";
				return false;
  			}else{
				this.usernameTips=null;
				this.usernameSrc="../../../static/img/ico3.png";
				return true;
			}
		},
		checkPw:function(){
			if(this.pw==null||this.pw==""||this.pw==undefined){
				this.pwTips="";
				this.pwSrc="../../../static/img/ico1.png";
				return false;
			}else if(this.pw.length<8){
				this.pwTips="";
				this.pwSrc="../../../static/img/ico2.png";
				return false;
			}else{
				this.pwTips=null;
				this.pwSrc="../../../static/img/ico3.png";
				return true;
			}
		},
		checkPw2:function(){
			if(this.pw2!=this.pw){
				this.pw2Tips="";
				this.pw2Src="../../../static/img/ico2.png";
				return false;
			}else{
				this.pw2Tips=null;
				this.pw2Src="../../../static/img/ico3.png";
				return true;
			}
		},
		checkPhone:function(){
			if(this.phone == null||this.phone.length!=11||isNaN(this.phone)){
				this.phoneTips="";
				this.phoneSrc="../../../static/img/ico2.png";
				return false;
			}else{
				this.phoneTips=null;
				this.phoneSrc="../../../static/img/ico3.png";
				return true;
			}
		},
		checkQQ:function(){
			if(this.QQ == null||this.QQ.length<=0||isNaN(this.QQ)){
				this.QQTips="QQ";
				this.QQSrc="../../../static/img/ico2.png";
				return false;
			}else{
				this.QQTips=null;
				this.QQSrc="../../../static/img/ico3.png";
				return true;
			}
		},
		checkCode:function(){
			if(this.code == null||this.code == ""||this.code == undefined|| "wrna" != this.code.toLowerCase()){
				this.codeTips="";
				this.codeSrc="../../../static/img/ico2.png";
				return false;
			}else{
				this.codeTips=null;
				this.codeSrc="../../../static/img/ico3.png";
				return true;
			}
		},
		register:function(){
			debugger
			var istrue;
			istrue = this.$options.methods.checkUsername.bind(this)();
			istrue = this.$options.methods.checkPw.bind(this)();
			istrue = this.$options.methods.checkPw2.bind(this)();
			istrue = this.$options.methods.checkPhone.bind(this)();
			istrue = this.$options.methods.checkQQ.bind(this)();
			istrue = this.$options.methods.checkCode.bind(this)();
			if(istrue){
				alert("");
			}else{
				alert("");
			}
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
	.registerCantainer
		width 100%
		height 630px
		background url("../../../static/img/container.jpg")
		.registerCantainer2
			width 58%
			height 630px
			margin 0 auto
			.registerBody
				width 100%
				height 445px
				background url("../../../static/img/c2.jpg") repeat-y
				.registerInfo
					margin 50px 0px 0px 20px
					width 50%
					height 370px
					display inline-block
					border-right 1px dashed #c19671
					table
						width 90%
						tr
							width 100%
							height 50px
							padding 5px
							td:nth-child(1)
								width 20%
								text-align right
								padding 0 15px
								font-size 17px
							td:nth-child(2)
								width 50%
								input
									width 100%
									height 38px
								input[name="code"]
									width 45%
									vertical-align:middle
								img
									vertical-align:middle
								a
									font-size 8px
									color black
							td:nth-child(3)
								width 30%
								font-size 10px
								img
									dispaly inline-block
									margin 0 10px
				.toLogin
					margin 50px 15px 0px 0px
					width 39%
					height 210px
					float right
					display inline-block
					font-size 14px
					span
						color red
</style>


