/*

*/
import Vue from 'vue'
import Router from 'vue-router'
import ContactUs from '../pages/ContactUs/ContactUs.vue'
import Hamburg from '../pages/Hamburg/Hamburg.vue'
import HomePage from '../pages/HomePage/HomePage.vue'
import KnowWe from '../pages/KnowWe/KnowWe.vue'
import MilkTea from '../pages/MilkTea/MilkTea.vue'
import News from '../pages/News/News.vue'
import Services from '../pages/Services/Services.vue'
import ShowRoom from '../pages/ShowRoom/ShowRoom.vue'
import Login from '../pages/Login/Login.vue'
import Register from '../pages/Register/Register.vue'
import LeaveWords from "../pages/LeaveWords/LeaveWords.vue"

//
Vue.use(Router)

export default new Router({
	//
	routes: [
		{
			path:"/",
			redirect:"/homepage"
		},
		{
			path:"/contactus",
			component:ContactUs,
			meta:{
				show:8
			}
		},
		{
			path:"/hamburg",
			component:Hamburg,
			meta:{
				show:4
			}
		},
		{
			path:"/homepage",
			component:HomePage,
			meta:{
				show:1
			}
		},
		{
			path:"/knowwe",
			component:KnowWe,
			meta:{
				show:2
			}
		},
		{
			path:"/milktea",
			component:MilkTea,
			meta:{
				show:6
			}
		},
		{
			path:"/news",
			component:News,
			meta:{
				show:5
			}
		},
		{
			path:"/services",
			component:Services,
			meta:{
				show:7
			}
		},
		{
			path:"/showroom",
			component:ShowRoom,
			meta:{
				show:3
			}
		},
		{
			path:"/login",
			component:Login
		},
		{
			path:"/register",
			component:Register
		},
		{
			path:"/leavewords",
			component:LeaveWords,
			meta:{
				show:8
			}
		}
	]
})



