<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="./img/banner-News.png">
			</div>
		</div>
		
		<div class="newsCantainer">
			<div class="newsCantainer2">
				<LoginHeader title="" :tips="tips"></LoginHeader>
				
				<div class="newsBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="newsRight">
						<div class="newsTitle"></div>
						
						<ul>
							<li>
								<div>
									<a href="#">!</a>
									<span> | 2015-05-01</span>
								</div>
								
								<div>
									"
								</div>
							</li>
							
							<li>
								<div>
									<a href="#">!</a>
									<span> | 2015-05-01</span>
								</div>
								
								<div>
									"
								</div>
							</li>
							
							<li>
								<div>
									<a href="#">!</a>
									<span> | 2015-05-01</span>
								</div>
								
								<div>
									"
								</div>
							</li>
							
							<li>
								<div>
									<a href="#">!</a>
									<span> | 2015-05-01</span>
								</div>
								
								<div>
									"
								</div>
							</li>
							
							<li>
								<div>
									<a href="#">!</a>
									<span> | 2015-05-01</span>
								</div>
								
								<div>
									"
								</div>
							</li>
						</ul>
						
						<div class="page">
							<a href="#" class="top">
								<i class="iconfont icon-left"></i>
								
							</a>
							<a href="#" class="current">1</a>
							<a href="#">2</a>
							<a href="#" class="bottom">
								
								<i class="iconfont icon-right"></i>
							</a>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["","",""],
			entry:[
				{
					message:"",
					hover:1,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				},
				{
					message:"",
					hover:0,
					skip:""
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.newsCantainer
	width 100%
	height 840px
	background url("../../../static/img/container.jpg")
	.newsCantainer2
		width 58%
		height 840px
		margin 0 auto
		.newsBody
			padding 20px 55px 20px 40px
			width 100%
			height 690px
			background url("../../../static/img/c2.jpg") repeat-y
			.newsRight
				width 80%
				float right
				.newsTitle
					font-size 20px
					border-bottom 1px dashed #c19671
					margin-bottom 25px
				ul
					list-style none
					font-size 13px
					li
						padding 15px 0
						border-bottom 1px dashed #c19671
						color black
						div:nth-child(1)
							margin-bottom 10px
							a
								font-size 20px
								color black
							span
								float right
				.page
					text-align center
					a
						display inline-block
						height 27px
						width 35px
						border 1px solid #e4e4e4
						color black
						border-radius 3px
						text-decoration none
					.top,.bottom
						width 95px
					a:hover
						background #f9ac43
						color white
					.current
						background #f9ac43
						color white
</style>


