<!--  -->
<template>
	<div>
		<div class="contactImg">
			<div class="bannerContact">
				<img src="./img/banner-Contact.png">
			</div>
		</div>
		
		<div class="cantactCantainer">
			<div class="cantactCantainer2">
				<LoginHeader title="" :tips="tips"></LoginHeader>
				
				<div class="ContactBody">
					<PageLeft :entry="entry"></PageLeft>
					<div class="ContactBodyRight">
						<div>
							
						</div>
						
						<div>
							<p></p>
							<p>12181003</p>
							<p>021-021-69180016</p>
							<p>www.qlzai.com</p>
							<p>
								400-021-1234
								<a href="#"></a>
							</p>
							<p>
								<img src="./img/img3.jpg" />
							</p>
						</div>
					</div>
				</div>
				
				<LoginFoot></LoginFoot>
			</div>
		</div>
	</div>
</template>

<script>
import LoginHeader from "../../components/LoginHeader/LoginHeader.vue"
import LoginFoot from "../../components/LoginFoot/LoginFoot.vue"
import PageLeft from "../../components/PageLeft/PageLeft.vue"
export default{
	components:{
		LoginHeader,
		LoginFoot,
		PageLeft
	},
	data:function(){
		return{
			tips:["","",""],
			entry:[
				{
					message:"",
					hover:1,
					skip:"/contactus"
				},
				{
					message:"",
					hover:0,
					skip:"/leavewords"
				}
			]
		}
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.contactImg
	width 100%
	background url("./img/inner.jpg")
	.bannerContact
		width 58%
		margin 0 auto
.cantactCantainer
	width 100%
	height 780px
	background url("../../../static/img/container.jpg")
	.cantactCantainer2
		width 58%
		height 780px
		margin 0 auto
		.ContactBody
			padding 20px 55px 20px 40px
			width 100%
			height 620px
			background url("../../../static/img/c2.jpg") repeat-y
			.ContactBodyRight
				float right
				div:nth-child(1)
					font-size 20px
					border-bottom 1px dashed #c19671
					margin-bottom 25px
				div:nth-child(2)
					p
						margin 10px 0 0 0
					p:nth-child(1)
						font-size 30px
					p:nth-child(5)
						font-size 30px
						color #900
						a
							font-size 10px
							color black
</style>


