<template>
	<div class="pageLeftContainer">
		<div class="pageLeftHeader"></div>
		<ul>
			<li v-for="en in entry" :key="en.id">
				<div :class="{hover:en.hover==1}">
					<router-link :to="{path:en.skip}">{{en.message}}</router-link>
				</div>
			</li>
		</ul>
		<div></div>
	</div>
</template>

<script>
export default{
	props:{
		entry:Array
	}
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
.pageLeftContainer
	width 150px
	height 800px
	display inline-block
	.pageLeftHeader
		height 41px
		background url("./img/tt.jpg") no-repeat center
	ul
		margin 0
		border 1px solid #e5d2b2
		padding 5px 0
		background #f0ebe7
		li
			list-style none
			width 100%
			height 35px
			line-height 35px
			position relative
			font-size 15px
			margin-bottom 1px
			border-bottom 1px solid #e4e0dc
			div
				width 110%
				position absolute
				left -7px
				a
					color black
					display block
					padding 0 7px 0 25px
					text-decoration none
				a:hover
					color white
					background  #eb9c13 url("./img/gt.png") no-repeat 125px center
			.hover
				background  #eb9c13 url("./img/gt.png") no-repeat 125px center
				a
					color white
	div:nth-child(3)
		height 41px
		background url("./img/boot.png") no-repeat center
</style>


